@import "variables";
body {
    overflow-x: hidden;
}

.header__text {
    display: none;
}

.progressbar {
    height: 2em;
    width: 85%;
    margin-top: 1.5em;
    background-color: #fff;
    border-radius: 25px;
    border: 10px solid $text_color;
    margin-left: 10%;
}

.numerotation {
    display: flex;
    justify-content: space-between;
    p {
        font-size: 2em;
        font-weight: 700;
        color: $text_color;
    }
}

.relative {
    position: relative;
    width: 100%;
}

#file {
    margin-top: 1.5em;
    width: 83.1%;
    margin-left: 10%;
    position: absolute;
    top: 8px;
    right: 5.9%;
}

.main {
    &__inform {
        text-align: center;
        margin-bottom: 3%;
        padding-top: 7em;
        &__img {
            width: 2.5em;
            height: 2.5em;
            &--active {
                border: 10px solid $btn_color;
                border-radius: 50%;
            }
        }
        a {
            color: white;
            padding: 0.6% 3%;
            background-color: $btn_color;
            border-radius: 5px;
            font-size: 2em;
            font-weight: 700;
        }
    }
    &__tags {
        display: flex;
        justify-content: space-between;
        width: 50%;
        margin: 3% auto;
    }
    &__tag {
        h3 {
            color: $btn_color;
            font-weight: 700;
        }
    }
    &__Preambule {
        width: 60%;
        margin: 2% auto;
        background-color: $bg_test;
        padding: 3% 2% 4%;
        border-radius: 5px;
        h3 {
            color: $btn_color;
            font-weight: 700;
            font-size: 2em;
        }
        p {
            margin: 2% 0;
        }
    }
    &__questionnaire {
        text-align: center;
        padding-top: 7em;
    }
    &__Resultat {
        padding-top: 7em;
        text-align: center;
    }
}

.affiche__resultat {
    max-width: 65%;
    margin: 2em auto;
    padding: 2em 3em;
    background-color: $bg_test;
    border-radius: 10px;
    h3 {
        font-size: 2em;
        font-weight: 700;
        color: $text_color;
    }
    p {
        font-size: 1.5em;
        font-weight: 500;
        margin: 1em 0;
    }
    span {
        font-size: 0.9em;
        color: $text_color;
    }
}

/////////////////js////////////////
.affiche {
    display: none;
}

form {
    border: 3px solid $bg_test;
    border-radius: 1em;
    padding: 2em 4em;
    max-width: 65%;
    margin: 3em auto;
}

.barNumber {
    border: 3px solid $bg_test;
    border-radius: 1em;
    margin-bottom: 1em;
}

.questions {
    p {
        font-weight: 700;
        font-size: 1.5em;
        color: $color2;
        height: 15vh;
        padding-top: 5%;
    }
}

.reponces {
    height: 24vh;
    display: flex;
    padding-top: 5%;
    justify-content: space-evenly;
    flex-wrap: wrap;
    label {
        margin-left: 0.4em;
        font-size: 2em;
    }
    .inputText {
        margin-left: 20%;
    }
    div {
        width: 45%;
    }
}

#repeter {
    border: 1px dotted $text_color;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 1.5em;
    font-weight: 600;
    background-color: $btn_color;
    margin: 2em auto;
}

.inputs {
    display: flex;
    justify-content: space-between;
    #precedent,
    #suivant,
    #resultat {
        border: 1px dotted $text_color;
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 1.5em;
        font-weight: 600;
        background-color: $btn_color;
    }
}

.answer-inputs {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 25vh;
}

.answer-inputs input[type="number"] {
    font-size: 16px;
    border: 1px solid #1078AD;
    width: 55%;
    padding: 10px;
}

.answer-inputs .input-span {
    font-size: 22px;
    border: 1px solid #1078AD;
    border-left: none;
    padding: 4px 10px 5px;
    color: #555555;
    background-color: #edf6fa;
}

.answer-inputs input[type="radio"] {
    -webkit-appearance: none;
}

.answer-inputs input[type="radio"]:checked+label {
    background-color: #1078AD;
    color: #F4CF1B;
}

.answer-inputs label {
    height: 80px;
    width: 120px;
    color: #1078AD;
    border-radius: 4px;
    border: 4px solid #1078AD;
    font-size: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 9px 0px 0;
    transition: .5s ease;
    margin-right: 50px;
}

.answer-inputs label span {
    font-size: 22px;
    position: relative;
    bottom: 5px;
}

input:disabled {
    cursor: not-allowed;
}